import styled from 'styled-components'
import breakpoint from "styled-components-breakpoint"

const Button = styled.button`
  border: 0;
  border-radius: .125rem;
  background-color: ${props => props.theme.color.s};
  color: ${props => props.theme.color.p};
  transition: ${props => props.theme.effects.transition};
  padding: .5rem 1rem;
  box-shadow: ${props => props.theme.effects.shadow[0]};
  &:hover{
    background-color: ${props => props.theme.color.p};
    color: white;
    box-shadow: ${props => props.theme.effects.shadow[1]};
    cursor: pointer;
  }
  &:focus{
    outline: none;
  }
`
const PhotoWrapper = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -999;
  background:
  linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/v1492714068/baby_minified.jpg');
  background-position: fixed;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: ${props => props.theme.dimensions.headerHeightSm};
  height: 95vh;
  display: flex;
  align-items: center;
  content: "";
  ${props => breakpoint('lg', props.theme.breakpoints)`
    margin-top: ${props => props.theme.dimensions.headerHeight};
  `}
`

const PageHeader = styled.h1`
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${props => props.theme.color.l};
  margin: 0 auto;
  text-align: center;
  height: ${props => props.theme.dimensions.pageHeaderHeight};
`
const ContentWrapper = styled.div`
  max-width: 960px;
  margin: 0 auto;
`
const TextWrapper = styled.div`
  background-color: ${props => props.theme.color.lo};
  padding: 1.5rem .8rem;
  border-radius: .125rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    margin: 0 1.45rem;
  `};
`

const SectionHeader = styled.h2`
  color: ${props => props.theme.color.p};
`
const Section = styled.section`
  margin-top: 2rem;
  // &:target{
  //   background-color: ${props => props.theme.color.p};
  //   color: ${props => props.theme.color.l};
  // }
  // &:target > h2{
  //   color: ${props => props.theme.color.l};
  // }
  &:target:before {
    content: "";
    display: block;
    height: ${props => props.theme.dimensions.headerHeight};
    margin: - ${props => props.theme.dimensions.headerHeight};
  }
`
export default {
  Button,
  PhotoWrapper,
  PageHeader,
  TextWrapper,
  ContentWrapper,
  Section,
  SectionHeader
}



